import 'bootswatch/dist/lux/bootstrap.min.css'
import 'bootstrap';
import 'bootstrap-icons/font/bootstrap-icons.css'
import '../src/css/index.scss';
import {loadPanel} from "./ts/playerPanel";

// @ts-ignore
import { Carousel } from 'bootstrap/dist/js/bootstrap.bundle.js';
import {Players} from "./ts/model/interfaces";
import {getPlayers} from "./ts/restClient";
// import {validateForm} from "./ts/formValidation";

let data: Players[];

window.addEventListener("load", async (event) => {
    event.preventDefault();
    data  = await getPlayers();
    await loadPanel(data);
    // validateForm();
});

menuCollapse();

function menuCollapse() {
    const menuList = document.getElementById("menuList");
    const navbarmenu = document.getElementById('navbarMenu');
    const links = document.querySelectorAll('#linkA,#linkB,#linkC,#linkD');

    links.forEach(
        (linkItem) => linkItem.addEventListener('click', () => {
            if (!menuList!.classList.contains('collapsed') && navbarmenu!.classList.contains('show')) {
                menuList!.click();
            }
        }));
}