import {Players} from "./model/interfaces";


const cardView = document.getElementById('cardView')! as HTMLElement;
const detailView = document.getElementById('detailView')!;

export function addPlayerCards(data: Players[]) {

    let cardCode = '';
    data!.forEach(p => {
        cardCode +=
            `<article class="card col-4 col-lg-3 flex-column me-3 mb-3 " style="width: 18rem;">
                <img src="${p.images.img1}"
            class="card-img-top" alt="spelerfoto">
            <div class="card-body">
            <h5 class="card-title">${p.description.title}</h5>
            <p class="card-text">${p.description.shortDescription}</p>
            </div>
            <ul class="list-group list-group-flush">
            <li class="list-group-item">Location : ${p.address.city}</li>
             <li class="list-group-item">Klasse : ${p.division.sub} </li>
           </ul>
            </article>
            `;
    });



    // data!.forEach(p => {
    //     cardCode +=
    //         `<article class="card col-4 col-lg-3 flex-column me-3 mb-3 " style="width: 18rem;">
    //             <img src="${p.images.img1}"
    //         class="card-img-top" alt="appartment">
    //         <div class="card-body">
    //         <h5 class="card-title">${p.description.title}</h5>
    //         <p class="card-text">${p.description.shortDescription}</p>
    //         </div>
    //         <ul class="list-group list-group-flush">
    //         <li class="list-group-item">Location : ${p.address.city}</li>
    //         <li class="list-group-item">Price : ${p.costs.monthlyRent} EUR / month</li>
    //         <li class="list-group-item align-self-center"><button data-location="${p.id}">More details</button></li>
    //         </ul>
    //         </article>
    //         `;
    // });
    cardView.innerHTML = cardCode;
}

