import {Players} from "./model/interfaces";


export async function getPlayers() {
    const online = true; //aangeven of het om de online of offline json file gaat
    if (online) { //TRUE
        const resp = await fetch('https://api.jsonbin.io/v3/b/672be6ccacd3cb34a8a3fed8',
            {
                method: "GET",
                headers: {
                    "X-Access-Key": "$2a$10$JBeqELcTBl.69PSfU/JCiuAIYYB/bCxJjVuJlQTJu4CDISDJMFJXa"
                }
            })
        if (!resp.ok) {
            throw Error("Something went wrong. GET operation failed.");
        } else {
            console.log("GET status 200");
            const retrieval = await resp.json();
            const data: Players[] = retrieval.record.players;
            return data;
        }
    } else { //FALSE
        const resp = await fetch('http://localhost:3000/players', {method: 'GET'});
        if (!resp.ok) {
            throw Error("Something went wrong. Offline GET operation failed.");

        } else {
            console.log("GET status 200");
            const data: Players[] = await resp.json();
            return data;
        }
    }


}